<template>
    <div class="flex flex-col space-y-1 m-1" :class="{'filter grayscale': type && type == 'not-received'}">

        <div class="shadow-xl rounded-xl w-32 h-44 p-0.5 flex items-center justify-center bg-gradient-to-br relative border-2" :class="{
            'from-red-300 via-red-800 to-red-300 border-red-600': rarity == 'covert', 
            'from-fuchsia-300 via-fuchsia-800 to-fuchsia-300 border-fuchsia-600': rarity == 'classified', 
            'from-purple-300 via-purple-800 to-purple-300 border-purple-600': rarity == 'restricted'
        }">

            <div class="rounded-lg overflow-hidden flex items-end w-full h-full relative bg-gradient-to-br to-black" :class="{
                'from-red-800': rarity == 'covert', 
                'from-fuchsia-800': rarity == 'classified', 
                'from-purple-800': rarity == 'restricted'
            }">

                <div class="w-full">
                    <img class="w-full" style="transform: scale(1.9) translateY(-21px);" :src="img" alt="">
                </div>

                <div class="py-1 px-1 text-sm absolute left-0 bottom-0 w-full bg-opacity-70 flex justify-center items-center" :class="{
                    'bg-red-500 text-red-300': rarity == 'covert', 
                    'bg-fuchsia-500 text-fuchsia-300': rarity == 'classified', 
                    'bg-purple-500 text-purple-300': rarity == 'restricted'
                }">
                    <span class="text-shadow-lg">Level 1</span>
                </div>

            </div>

            <span v-if="isUpgradable" class="absolute -left-1 -bottom-1 animate__animated animate__bounce animate__infinite">
                <img class="w-5 transform -rotate-90" :src="require('@/assets/img/game/upgrade-lvl-arrow.png')" alt="arrow">
            </span>

        </div>

        <div v-if="type == 'received'" class="overflow-hidden relative flex p-1 rounded-lg text-white font-bold" :class="isUpgradable ? 'bg-green-500': 'bg-gray-900'">
            <div v-if="!isUpgradable" class="bg-blue-500 h-full absolute top-0 left-0 z-0" :style="'width:' + (count / needCount) * 100 + '%'"></div>
            <div class="flex w-full items-center justify-center space-x-1 z-10">
                <span>{{count}}</span>
                <span>/</span>
                <span>{{needCount}}</span>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {

            }   
        },
        computed: {
            isUpgradable() {
                if(this.type == 'not-received') return
                return this.count >= this.needCount
            }
        },
        props: {
            img: {
                type: String,
                required: true
            },
            rarity: {
                type: String,
                required: true
            },
            type: {
                type: String,
                required: true
            },
            count: {
                type: Number
            },
            needCount: {
                type: Number
            },
        }
    }
</script>
