import axios from "axios"

export default {
    state: {
        user: null
    },
          
    mutations: {
        updateUser(state, user) {
            state.user = user
        }
    },
          
    actions: {
        async fetchUser(ctx) {
            await axios.get('/user')
            .then((response) => {
                ctx.commit('updateUser', response.data.user)
            });
        },
    },
          
    getters: {
        user: function(state) {
            return state.user
        }
    }
}