<template>
    <div class="bg-blue-600 w-full h-full flex items-center justify-center">
        <div class="w-full h-full relative " :style="'background: center / cover no-repeat url(' + require('@/assets/img/game/bg.png') + ')'" style="max-width: 1000px !important;"> 
            <!-- max-height: 800px !important -->
            <div class="flex flex-col items-center justify-center bg-black bg-opacity-50 backdrop-filter backdrop-blur-sm w-full h-full">
                <Header />
                <div class="flex-grow overflow-y-auto w-full h-full">
                    <router-view :key="$route.path"></router-view>
                </div>
                <Footer />
            </div>
        </div>
    </div>
</template>

<script>
    import bridge from '@vkontakte/vk-bridge'
    import Header from '@/components/layout/Header.vue'
    import Footer from '@/components/layout/Footer.vue'

    export default {
        name: 'App',
        components: {Header, Footer},

        data() {
            return {

            }
        },

        mounted() {
            // Слушаем все события
            bridge.subscribe(event => {
                if (!event.detail) return
                const { type, data } = event.detail
                if (!type || !data) return
                const actions = {
                    VKWebAppGetUserInfoResult: data => (this.state.user = data),
                    VKWebAppGetClientVersionResult: data =>
                        (this.state.platform = data.platform),
                    VKWebAppAllowNotificationsResult: data =>
                        (this.state.notifEnabled = true),
                    VKWebAppDenyNotificationsResult: data =>
                        (this.state.notifEnabled = false),
                    VKWebAppAllowMessagesFromGroupResult: data =>
                        (this.state.messagesEnabled = true),
                    VKWebAppAllowMessagesFromGroupFailed: data =>
                        (this.state.messagesEnabled = false),
                }
                actions[type] ? actions[type](data) : console.log(event.detail.type)
            })
            // Запрашиваем данные пользователя
            bridge.send('VKWebAppGetUserInfo')
            bridge.send('VKWebAppGetClientVersion')
            // // Включены ли уведомления
            // this.state.notifEnabled = this.$route.query.vk_are_notifications_enabled == 0 ? false : true
        }
    }
</script>

<style>
    html, body {
        width: 100%;
        height: 100%;
    }
</style>